.TodoSearch {
  background: #f9fbfc;
  border: 0;
  border-radius: 2px;
  border: 2px solid #202329;
  margin: 10px 24px;
  height: 36px;
  width: calc(100% - 62px);
  font-size: 24px;
  text-align: center;
  font-family: "Roboto";
  font-weight: 400;
  color: #1e1e1f;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  padding: 0;
}

.TodoSearch::placeholder {
  color: #a5a5a5;
  font-family: "Roboto";
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: #2158ff;
}

.TodoSearch:disabled {
  opacity: 25%;
}
